	module.exports = {"data":{"uiComponentsYaml":{"title":"Tooltip","category":"UI components","examples":[{"title":"Usage","introduction":"The state of the tooltip can be controlled using `isOpen`.\n","subTitle":"Example","react":"import React from 'react';\nimport { Tooltip } from 'reactstrap';\n\nexport default class Example extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.toggle = this.toggle.bind(this);\n    this.state = {\n      tooltipOpen: false\n    };\n  }\n\n  toggle() {\n    this.setState({\n      tooltipOpen: !this.state.tooltipOpen\n    });\n  }\n\n  render() {\n    return (\n      <div>\n        <p>This is an example <a href=\"#\" id=\"TooltipExample\">tooltip</a>.</p>\n        <Tooltip\n          placement=\"bottom\"\n          isOpen={this.state.tooltipOpen}\n          target=\"TooltipExample\"\n          toggle={this.toggle}\n        >\n          Tooltip text bottom\n        </Tooltip>\n      </div>\n    );\n  }\n}\n","angular":"<div style=\"padding: 50px\">\n  <a type=\"button\"\n          tooltip=\"Vivamus sagittis lacus vel augue laoreet rutrum faucibus.\"\n          placement=\"bottom\">\n    tooltip.\n  </a>\n</div>\n","component":null,"css":null,"html":null,"actualHtml":null},{"title":"Uncontrolled variant","introduction":"For simple cases, an uncontrolled tooltip which manages its own state can\nbe used.\n","subTitle":"Example","react":"import React from 'react';\nimport { UncontrolledTooltip } from 'reactstrap';\n\nexport default function Example() {\n  return (\n    <div>\n      <p>This is an example <a href=\"#\" id=\"UncontrolledTooltipExample\">tooltip</a>.</p>\n      <UncontrolledTooltip placement=\"bottom\" target=\"UncontrolledTooltipExample\">\n        Uncontrolled tooltip text\n      </UncontrolledTooltip>\n    </div>\n  );\n}\n","angular":null,"component":null,"css":null,"html":"<div style=\"padding: 50px\">\n  <a href=\"#\"\n    data-toggle=\"tooltip\"\n    data-placement=\"bottom\"\n    title=\"Tooltip on bottom\"\n  >\n    Tooltip\n  </a>\n  <script>$(function () {$('[data-toggle=\"tooltip\"]').tooltip()})</script>\n</div>\n","actualHtml":null},{"title":"Positioning","introduction":"The position of the tooltip can be controlled.\n","subTitle":"Example","react":"import React from 'react';\nimport { UncontrolledTooltip } from 'reactstrap';\n\nexport default function Example() {\n  return (\n    <div>\n      <p>\n        <span id=\"UncontrolledTooltipExampleTop\">top</span>\n        <UncontrolledTooltip placement=\"top\" target=\"UncontrolledTooltipExampleTop\">\n          Top\n        </UncontrolledTooltip>\n        <span id=\"UncontrolledTooltipExampleRight\">right</span>\n        <UncontrolledTooltip placement=\"right\" target=\"UncontrolledTooltipExampleRight\">\n          Right\n        </UncontrolledTooltip>\n        <span id=\"UncontrolledTooltipExampleBottom\">bottom</span>\n        <UncontrolledTooltip placement=\"bottom\" target=\"UncontrolledTooltipExampleBottom\">\n          Bottom\n        </UncontrolledTooltip>\n        <span id=\"UncontrolledTooltipExampleLeft\">left</span>\n        <UncontrolledTooltip placement=\"left\" target=\"UncontrolledTooltipExampleLeft\">\n          Left\n        </UncontrolledTooltip>\n      </p>\n    </div>\n  );\n}\n","angular":"<div style=\"padding: 50px\">\n  <Button type=\"button\" class=\"btn btn-default btn-secondary mb-2\"\n          tooltip=\"Vivamus sagittis lacus vel augue laoreet rutrum faucibus.\"\n          placement=\"top\">\n    Tooltip on top\n  </Button>\n\n  <Button type=\"button\" class=\"btn btn-default btn-secondary mb-2\"\n          tooltip=\"Vivamus sagittis lacus vel augue laoreet rutrum faucibus.\"\n          placement=\"right\">\n    Tooltip on right\n  </Button>\n\n  <Button type=\"button\" class=\"btn btn-default btn-secondary mb-2\"\n          tooltip=\"Vivamus sagittis lacus vel augue laoreet rutrum faucibus.\"\n          placement=\"auto\">\n    Tooltip auto\n  </Button>\n\n  <Button type=\"button\" class=\"btn btn-default btn-secondary mb-2\"\n          tooltip=\"Vivamus sagittis lacus vel augue laoreet rutrum faucibus.\"\n          placement=\"left\">\n    Tooltip on left\n  </Button>\n\n  <Button type=\"button\" class=\"btn btn-default btn-secondary mb-2\"\n          tooltip=\"Vivamus sagittis lacus vel augue laoreet rutrum faucibus.\"\n          placement=\"bottom\">\n    Tooltip on bottom\n  </Button>\n</div>\n","component":null,"css":".container-size{\n  height: 80px;\n}\n","html":"<div style=\"padding: 50px\">\n  <a href=\"#\"\n    data-toggle=\"tooltip\"\n    data-placement=\"top\"\n    title=\"Tooltip on top\"\n  >\n    &nbsp;&nbsp;&nbsp; Tooltip on top  &nbsp;&nbsp;&nbsp;\n  </a>\n  <a href=\"#\"\n    data-toggle=\"tooltip\"\n    data-placement=\"right\"\n    title=\"Tooltip on right\"\n  >\n    &nbsp;&nbsp;&nbsp; Tooltip on right &nbsp;&nbsp;&nbsp;\n  </a>\n  <a href=\"#\"\n    data-toggle=\"tooltip\"\n    data-placement=\"bottom\"\n    title=\"Tooltip on bottom\"\n  >\n    &nbsp;&nbsp;&nbsp; Tooltip on bottom &nbsp;&nbsp;&nbsp;\n  </a>\n  <a href=\"#\"\n    data-toggle=\"tooltip\"\n    data-placement=\"left\"\n    title=\"Tooltip on left\"\n  >\n    &nbsp;&nbsp;&nbsp; Tooltip on left &nbsp;&nbsp;&nbsp;\n  </a>\n</div>\n<script>$(function () {$('[data-toggle=\"tooltip\"]').tooltip()})</script>\n","actualHtml":null}],"fields":{"slug":"/ui-components/tooltip/","preHtml":"<p>Tooltips can be attached to any element. When you hover the element with your\nmouse, the title attribute is displayed in a little box next to the element\nthat can be styled. Tooltips are also useful for form elements, to show some\nadditional information in the context of each field and can be style with a\nheading.</p>\n","postHtml":"\n"}}},"pathContext":{"slug":"/ui-components/tooltip/"}}

